body,html{width:100%;height:100%;overflow:hidden;background-color:#999;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI","微软雅黑", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"}div,h1,h2,h3,h4,ul,li,table,tt,td,th,tbody,p{padding:0;margin:0;list-style:none;box-sizing:border-box}#board{float:left;padding:10px}.board{display:inline-table;width:540px;height:540px;border-collapse:collapse}.board td{width:60px;height:60px;border:3px solid black;text-align:center;color:white;font-weight:600;cursor:pointer;-webkit-transition:all 0.2s ease-in-out;transition:all 0.2s ease-in-out}.board td:hover{background-color:green;-webkit-transform:scale(1.3);transform:scale(1.3)}.board td.focus{background-color:red}



